<template>
  <div class="edit-container">
    <shop-form ref="form" :model="form" :rules="rules" class="c_content">
      <shop-item label="持卡人" prop="name">
        <template slot="valid" slot-scope="{ valid }">
          <shop-input v-model="form.name" placeholder="请输入姓名" :state="valid" />
        </template>
      </shop-item>
      <shop-item label="身份证号" prop="IdCard">
        <template slot="valid" slot-scope="{ valid }">
          <shop-input v-model="form.IdCard" :state="valid" placeholder="请输入持卡人身份证号码" />
        </template>
      </shop-item>
      <shop-item label="银行" prop="bank">
        <template slot="valid" slot-scope="{ valid }">
          <div :style="{width:'100%',height:'auto'}" @click="show=true">
            <shop-input
              v-model="form.bank"
              rightIcon="iconxia"
              :state="valid"
              placeholder="选择银行"
              readonly
            />
          </div>
        </template>
      </shop-item>
      <shop-item label="卡号" prop="card">
        <template slot="valid" slot-scope="{ valid }">
          <shop-input v-model="form.card" :state="valid" placeholder="持卡人本人银行卡号" />
        </template>
      </shop-item>
      <div class="footer">
        <button type="submit" @click.prevent="submitForm">提交</button>
      </div>
    </shop-form>
    <van-action-sheet
      v-model="show"
      :actions="actions"
      @select="onSelect"
      cancel-text="取消"
      close-on-click-action
    />
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      show: false,
      actions: [{ name: "选项一" }, { name: "选项二" }, { name: "选项三" }],
      form: {
        name: "",
        IdCard: "",
        bank: "",
        card: "",
      },
      rules: {
        name: [{ required: true, trigger: "blur" }],
        IdCard: [{ required: true, trigger: "blur" }],
        bank: [{ required: true, trigger: "select" }],
        card: [{ required: true, trigger: "blur" }],
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.form.bank = item.name;
      this.show = false;
      this.$toast(item.name);
    },
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          console.log(1);
        }
      });
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.edit-container {
  height: 100vh;
  background-color: #ffffff;
  .c_content {
    height: 100%;
    padding: 12px 20px;
  }
}
</style>


